.generic-title {
    color: var(--primary-title-color);
    font-size: 22px;
    text-align: $start;
    padding-bottom: 40px;
    position: relative;
    &:after {
      content: "";
      background-color: #c28a33;
      height: 7px;
      width: 40px;
      position: absolute;
      #{$start}: 0;
      bottom: 0;
    }
    &:before {
      content: "";
      background-color: var(--primary-title-color);
      height: 1px;
      width: 30px;
      position: absolute;
      #{$start}: -35px;
      top: 15px;
    }
  }

  .generic-description {
    color: var(--secondary-title-color);
    font-size: 51px;
    font-weight: bold;
    text-align: $start;
    margin-bottom: 45px;
  }

input,
select,
textarea {
  width: 100%;
  padding-bottom: 10px;
  border: 0;
  border-bottom: 2px solid var(--secondary-title-color);
  color: var(--secondary-title-color);
  outline: none;
  background-color: transparent;
  font-size: 20px;

  &::placeholder {
    color: var(--secondary-title-color);
  }
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type=number] {
    -moz-appearance: textfield;
}